<template>
  <section class="recommendContainer">
    <!-- 课程列表 -->
    
    <work-area-item v-if="WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST.length > 0" :areaList="WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST"></work-area-item>

    <section class="bg_img" v-if="WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST.length === 0">
      <img :src="noList" alt="没有作业啦">
      <p>讲师还没有批改我的作业哦~</p>
    </section>
    <div style="padding: 10px 0 60px 0;text-align: center;background-color: #fff;color: #777;" v-if="falgNoData">没有更多了~</div>
  </section>
</template>
<script>
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('workRangeModule')
  import workAreaItem from '@/components/workAreaItem'
  export default {
    components: {
      workAreaItem
    },
    data() {
      return {
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        noList: require('../../assets/img/kognbaiye_wodezuoye.png')
      }
    },
    computed: {
      ...mapState([
        'WORK_RANGE_MINE_OBJECT',
      ]),
      workRangeSavePosition(){
        return this.$store.state.workRangeSavePosition.myWorkInRange
      },
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    destroyed () {
      window.onscroll = () => {
        return
      }
    },
    mounted () {
      window.onscroll = () => {
        this.dropDown()
      }
      setTimeout(() => {
        document.documentElement.scrollTop = this.workRangeSavePosition
      }, 100)
    },
    watch: {
      LOGIN_USER_OBJECT(state){
        if(state){
          // 页面加载的等待状态
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })

          this.$store.state.workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_PAGE = 1
          this.$store.state.workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST = []

          let params = {
            page: this.WORK_RANGE_MINE_OBJECT.WORK_MINE_PAGE
          }
          this.WORK_RANGE_MINE( params ).then(data => {
            this.$Indicator.close()
            window.onscroll = () => {
              this.dropDown()
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
    },
    methods: {
      ...mapActions([
        'WORK_RANGE_MINE'
      ]),
      // 手势上滑加载数据
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.falgNoData = true
          this.WORK_RANGE_MINE({
            page: ++this.$store.state.workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_PAGE
          }).then(data => {
            if (this.pagination) {
              if (data.data.list.length > 0) {
                this.$Indicator.close()
                this.falgNoData = false
              } else {
                this.$Toast({
                  message: '没有更多了',
                  position: 'middle',
                  duration: 1000
                })
                this.$Indicator.close()
                this.pagination = false
                this.showData = true
                this.falgNoData = true
              }
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
    },
    created () {
      
      if(this.LOGIN_USER_OBJECT && this.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST.length === 0){
        console.log('加载')
        // 页面加载的等待状态
        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })
        let params = {
          page: 1
        }
        this.WORK_RANGE_MINE( params ).then(data => {
          this.$Indicator.close()
        }).catch(err => {
          console.log(err)
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import url('../../assets/css/workArea');
  .recommendContainer{
    padding-bottom: 100px;
  }
  .coreClass{
    &>img{
      width: 88px !important;
      height: 88px !important;
    }
    &.trash{
      &>img{
        width: 38px !important;
        height: 38px !important;
      }
    }
  }
/* 无列表的情况下，显示背景图片 */
.bg_img {
    width: 100%;
    text-align: center;
}
.bg_img>img{
    margin-top:30px;
    width: 330px;
    height: 316px;
}
.bg_img>p{
    margin-top: 20px;
    font-size: 30px;
    color: #464646;
}
</style>



